.modal-container{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(123, 123, 123, 0.2);
    z-index: 1000;
   display: -ms-flexbox;
   display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
        
}

.modal-wrapper{
    width: 400px;
    padding: 30px 40px;
    font: 15px 'Open Sans';
    background-color: #fff;
    color: #000;
    border-radius: 10px;
}

.modal-content{
    margin-bottom: 40px;
}

.modal-container button{
    margin: 0;
}


