.list-wrapper{
    min-height: 200px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    margin-top: 15px;
    background: #f7f7f7;
    border-radius: 10px;
    padding: 20px;
    border: 1px solid #dddddd;
}

.list-wrapper.empty{
    height: auto;
}

.empty-block{
    padding: 30px 100px;
}


